export default function Tags(tags) {
  return `
        <div>
        <h3>Tags</h3>
        ${tags.docs
          .map(tag => {
            const tagData = tag.data();
            return `
                <ul class='main-content__tags'>
                    <li>${tagData.tagName}</li>
                </ul>
                `;
          })
          .join('')}
        </div>
        `;
}
